import QNote from './QNote';
import QScore, {chineseNumbers} from './QScore';
import QInterval from './QInterval';

const chordTypes = [3,7,9,11]
const chordNames = {
	3: ["减三和弦", "小三和弦", "大三和弦", "增三和弦"],
	7: ["减七和弦", "半减七和弦", "小七和弦", "属七和弦", "小大七和弦", "大七和弦", "增属七和弦", "增大七和弦"],
	9: ["小九和弦", "属九和弦", "大九和弦"],
	11: ["小十一和弦", "属十一和弦", "大十一和弦"],
}
const chordInfos={
	"减三和弦": {types:["小三度","减五度"], sufix:"dim"}, //Cdim
	"小三和弦": {types:["小三度","纯五度"], sufix:"m"}, //Cm
	"大三和弦": {types:["大三度","纯五度"], sufix:""}, //C
	"增三和弦": {types:["大三度","增五度"], sufix:"aug"}, //C+ Caug

	"减七和弦": {types:["减三和弦","减七度"], sufix:"dim7"}, //Cdim7、C7
	"半减七和弦": {types:["减三和弦","小七度"], sufix:"ø7"}, //Cø7 
	"小七和弦": {types:["小三和弦","小七度"], sufix:"m7"}, //Cmin7、Cmi7、Cm7、C-7
	"属七和弦": {types:["大三和弦","小七度"], sufix:"7"}, //Cdominant7、Cdom7、C7
	"小大七和弦": {types:["小三和弦","大七度"], sufix:"mmaj7"}, //C-Δ7、Cm(maj7)、C-(maj7)
	"大七和弦": {types:["大三和弦","大七度"], sufix:"maj7"}, //CΔ7、Cmaj7、Cma7、CM7
	"增属七和弦": {types:["增三和弦","小七度"], sufix:"aug7"}, //Caug7
	"增大七和弦": {types:["增三和弦","大七度"], sufix:"maj7(#5)"}, //Cmaj7(#5) CMaj7+5、CM7+ Caug(maj7)、C+(maj7)、C+Δ7
	"增七和弦": {types:["增三和弦","增七度"], sufix:"+7"}, // Caug7、C+7

	"小九和弦": {types:["小七和弦","大九度"], sufix:"m9"}, //Cm9
	"属九和弦": {types:["属七和弦","大九度"], sufix:"9"}, //C9
	"大九和弦": {types:["大七和弦","大九度"], sufix:"maj9"}, //Cmaj9
	// "增九和弦": {types:["增七和弦","增九度"], sufix:"+7"}, //C+9

	"小十一和弦": {types:["小九和弦","纯十一度"], sufix:"m11"}, //Cm11
	"属十一和弦": {types:["属九和弦","纯十一度"], sufix:"11"}, //C11
	"大十一和弦": {types:["大九和弦","纯十一度"], sufix:"maj11"}, //Cmaj11
}


class QChord extends QScore {
	constructor() {
		super()
		this.english = ""
		// this.
		this.type=3 //3,7,9,11
		this.transpose=0 //0,1,2,3
	}
	static randomChordNames(config, count, except) {
		var res = []
		for (var i = 0; i < 10; i++) {
			var type = config && config.types ? config.types[QNote.random(config.types.length-1)] : null
			var alter = config && config.alters ? config.alters[QNote.random(config.alters.length-1)] : null
			var chordType = QChord.getChordName(type)
			var chordName = chordType.name
			if (chordName !== except && res.indexOf(chordName)<0) {
				res.push(chordName)
				if (res.length>=count) {
					break
				}
			}
		}
	
		return res
	}
	/*
	params:
		chord: {name: string, english: string, notes:[{step, octave, alter}, ...]}
		level: 第1, 2, 3转位
	return:
		{name: string, english: string, notes:[{step, octave, alter}, ...]}
	*/
	transposeChord(level) {
		if (level !== 1 && level !== 2 && level !== 3) {
			return this
		}
		// console.log(this)
		var res = this //NoteUtil.deepCopy(chord)
		var isTriad = (res.notes.length===3)
		var chordType = isTriad ? "三" : "七"
		var newType
		if (res.notes.length===3) {
			if (level===1) {
				newType = "六"
			}else{
				newType = "四六"
			}
		}else{
			if (level===1) {
				newType = "五六"
			}else if(level===2){
				newType = "三四"
			}else{
				newType = "二"
			}
		}
		res.description = "("+res.name+" - 第"+chineseNumbers[level-1]+"转位)"
		res.name = res.name.replace(chordType, newType)

		for (var i = 0; i < level; i++) {
			var note = res.notes[0]
			note.octave += 1
			res.notes.splice(0,1)
			res.notes.push(note)

			note.setNoteValueLineAcc("treble")
		}

		var rootNote = res.notes[0]
		res.english += "/"+rootNote.textOfAlter()+rootNote.textOfStep()
		// console.log(res)
		return res
	}
	/*
	params:
		chordeType: =null:随机， 或三、七、九、十一、十三和弦
	return:
		{
			type: 3,7,9,11,13
			name: 和弦名字
		}
	*/
	static getChordName(chordType) {
		var names = chordNames[chordType]
		if (!names) {
			chordType = chordTypes[QNote.random(chordTypes.length-1)]
			names = chordNames[chordType]
		}
		var chordName = names[QNote.random(names.length-1)]
		return {type: chordType, name: chordName}
	}

	/*
	return:
		{name: string, english: string, notes:[{step, octave, alter}, ...]}
	*/
	static chordFromRoot(rootNote, chordName) {
		var chordInfo = chordInfos[chordName]
		if (chordInfo) {
			var english = rootNote.textOfAlter()+rootNote.textOfStep()+chordInfo.sufix
			//var res = {name: chordName, english: english, notes:[]}
			var res = new QChord()
			res.name = chordName
			res.english = english
			for (var i = 0; i < chordInfo.types.length; i++) {
				var type = chordInfo.types[i]
				if (chordInfos[type]) {
					var subChord = QChord.chordFromRoot(rootNote, type)
					if (subChord) {
						res.notes=subChord.notes
					}else{
						return
					}
				}else {
					var interval = QInterval.intervalFromRoot(rootNote, type)
					// console.log("intervalFromRoot:"+type)
					// console.log(interval)
					if (interval) {
						if (res.notes.length===0) {
							res.notes.push(interval.root)
						}
						res.notes.push(interval.head)
					}else{
						return
					}
				}
			}
			return res
		}
	}
	/*
	params:
		config: null为随机和弦
			{
				types: null 随机和弦, 或[3、7、9、11、13] 和弦, 
				transpose: null 随机转位，或第[0，1，2，3]转位
			}
	return:
		{
			name: 和弦名字
			description: 描述
			notes: [{step, octave, alter, line, value},...]
		}
	*/
	static randomChord(config) {
		var type = config && config.types ? config.types[QNote.random(config.types.length-1)] : null
		var alter = config && config.alters ? config.alters[QNote.random(config.alters.length-1)] : null
		var note = QNote.randomNote(alter)

		var chordType = QChord.getChordName(type)
		// console.log(type)

		var count = 0
		while(count<7*10) {
			// console.log(count)
			// console.log(note)
			var chord = QChord.chordFromRoot(note, chordType.name)
			if (chord) {
				var transposeLevel = 0
				if (chordType.type<9) {
					transposeLevel = config && config.transposes ? config.transposes[QNote.random(config.transposes.length-1)] : QNote.random(chordType.type===3 ? 2 : 3)
				}
				if (transposeLevel>0) {
					chord = chord.transposeChord(transposeLevel)
				}
				var wrong = false
				if (config.alters) {
					for (var i = 0; i < chord.notes.length; i++) {
						var n = chord.notes[i]
						if (config.alters.indexOf(n.alter)<0) {
							wrong = true
							// console.log(chord)
							break
						}
					}
				}
				if (!wrong) {
					return chord
				}
			}
			if (transposeLevel>0) {
				note.octave = 4
			}
			if(note.alter<1) {
				note.alter += 1
			}else if (note.step<6) {
				note.step += 1
				note.alter = -1
			}else{
				note.step = 0
				note.alter = -1
				// if (note.octave<4) {
				// 	note.octave += 1
				// }
			}
			note.setNoteValueLineAcc()
			count += 1

			if (count>20) {
				chordType = QChord.getChordName(type)
			}
		}
	}
}
export default QChord;



